*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.clearfix::after{
    content: "";
    display: block;
    clear: both;
}
.header_title{
    width: 100%;
    height: 100px;
    background: rgb(238, 238, 238);
}
.title_sutdent{
    width: 100%;
    height: 20px;
    background: rgb(245 , 245, 245);
    border-top: 1px solid rgba(0, 0, 0, 0);
}
.header_title h2{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: black;
    margin-left: 10%;
    /* margin-top: 10px; */
    line-height: 100px;
}

.body{
    width: 1200px;
    padding-top: 50px;
    padding-bottom: 50px;
    /* background: red; */
    margin-left: 10%;
}
ul li{
   list-style: none;
}
.body .nav ul li a{
    text-decoration: none;
}
.body .nav ul li a{
    border-radius: 1px;
    display: block;
    border: 1px solid gray;
    color: rgb(148, 147, 147);
    width: 200px;
    height: 40px;
    text-align: center;
    line-height:40px ;
    cursor: pointer;
}
.body .nav ul li a:hover{
    color: rgb(73, 156, 138)
}
.body .nav{
    width:200px;
    /* background: rgb(51, 122, 183); */
    float: left;
}
.body .nav ul li .active{
    background: rgb(51, 122, 183);
    color: aliceblue;
}
.body_content{
    margin-left: 60px;
    width: 900px;
    height: 500px;
    /* background: yellow; */
    float: left;
}
