*{
    padding: 0;
    margin: 0;
}
body{
    background: #f7fcff;
}
.box{
    border: 1px solid white;
    width: 500px;
    height: 499px;
    background:white;
    margin: 0 auto;
    margin-top: 150px;
}
.title{
    width: 200px;
    margin: 0 auto;
    margin-top: 50px;
}
.title h2{
    width: 200px;
}
#username,#password{
    display: block;
    width: 290px;
    height: 50px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
    margin: 0 auto;
}
#username{
    margin-top: 50px;
}
#password{
    margin-top: 20px;
    margin-bottom: 30px;
}
#btn{
    display: block;
    width: 300px;
    height: 50px;
    margin: 0 auto;
}
.zaxiang{
    width: 300px;
    height: 50px;
    border-bottom: 1px solid #555;
    margin: 0 auto;
    margin-top: 10px;
    font-size: 15px;
}
.zaxiang a{
        text-decoration: none;
        color: orange;
}
.zaxiang a:hover{
    text-decoration:underline;
}
.zaxiang span{
    float: left;
}
.zaxiang a:nth-last-child(1){
    float: right;
    color: #CCCCCC;
}
.b{
    margin-top:30px;
}