.addstudent_context{
    width:80%;
    height: 90%;
    /* background: red;  */
    margin: 0 auto;
    padding:30px;
}
.addstudent_context .addstudent_context_item{
    width: 100%;
    height: 30px;
    margin-top: 20px;
    /* background: yellow; */
}
.addstudent_context .addstudent_context_item input{
    height: 100%;
    float: left;
    width: 30%;
    outline: none;
}
.addstudent_context .addstudent_context_item_title{
    float: left;
    width: 100px;
    height: 30px;
    line-height: 30px;
    text-align: end;
    margin-right: 30px;
    /* background: lawngreen; */
}
.addstudent_context_item span{
    margin-left: 30px;
    height: 30px;
    line-height: 30px;
    width: 100px;
    color: red;
}