.addstudent_body_content{
    margin-bottom: 10px;
    width: 100%;
    height: 500px;
    border: 1px solid gray;
    border-radius: 5px;
    /* background: #bfa; */
}
.title_sutdent{
    width: 100%;
    height: 30px;
    line-height: 30px;
    padding-left: 10px;
    margin-top: 2px;
    border-bottom: 1px solid rgb(0, 0, 0);
}
.content_list{
    padding-left: 10px;
}
table a{
    text-decoration: none;
    color: rgb(51, 122, 183);
}
table a:hover{
    opacity: 0.5;
}
table tr td,table thead td{
    width: 129px;
    padding-top: 5px;
    padding-bottom: 5px;
}
table tbody tr:nth-child(odd){
    background: rgb(247, 246, 246);
}
table{
	border-collapse: collapse; 
}
table tbody tr{
    border-top: 1px solid rgb(141, 139, 139);
}
.studentslist_delete,
.studentslist_revise{
    width: 50px;
}
.student_list_search{
    width: 100%;
    height: 45px;
    font-size: 18px;
    border-bottom: 1px solid black;
    padding-left: 10px;
    
}
.student_list_search span{
    width: 100%;
    height: 45px;
    font-size: 18px;  
    line-height: 45px;
} 
.student_list_search input{
    border: none;
    border-bottom: 1px solid black;
    line-height: 30px;
    height: 30px;
    outline: none;
    margin-left: 5px;
    padding: 5px;
}
.pn{
    padding-bottom: 20px;
}
.student_list_search input{
    background: none;
}